.EventPage {
    padding: 0 1.25rem;

    a {
        color: #00b073;
        text-decoration: none;
        transition: opacity 0.2s linear;

        &:hover {
            opacity: 0.7;
        }
    }
}

.CurrentEvent {
    padding: 12px 14px;
    margin-top: 10px;
    text-align: center;

    border-radius: 2px;
    background-color: #161616;

    font-size: 15px;
    font-weight: 700;
}
